html {
  height: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

#root {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper {
  display: flex;
  border-radius: 3pt;
  height: 100%;
}
@media (max-width: 500px) {
  .app-wrapper {
    flex-direction: column;
  }
}
html {
  height: 100%;
}
.badge-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.bio-wrapper {
    padding-top: 50pt;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 320pt;
}

.bio-pane {
    display: flex;
    padding-top: 30pt;
    padding-left: 20pt;
    padding-right: 20pt;
    text-align: justify;
    flex-direction: column;
    height: 100%;
}

.heading {
    color: white;
}
.bio-pane {
    display: flex;
    flex: 2pt 1;
    justify-content: flex-start;
    background: #084B8A;
    color: white;
    height: inherit;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 200pt;
    width: 300pt;
    background: "white";
    margin-left: 50px;
    margin-bottom: 50px;
}

.project-name {
    text-align: center;
    margin-top: 10px;
}

.short-desc {
    text-align: justify;
    padding-right: 30px;
    padding-left: 30px;    
}
.album-wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    flex-wrap: wrap;
}
.projects-pane {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: "tint1";
    flex: 5 1;
    min-width: 320pt;
    overflow-y: scroll;
}
